<template>
    <div class="container">
        <h2>countdown 倒计时组件</h2>
        <h3>倒计时组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'countdown',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'className',
                    desc: '组件最外层className',
                    type: 'String,Array',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'isShowDay',
                    desc: '是否显示天数',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                },
                {
                    param: 'isShowMillSeconds',
                    desc: '是否显示毫秒',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                },
                {
                    param: 'endTime',
                    desc: '倒计时结束时间',
                    type: 'String',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'classNameText',
                    desc: '每个时间自定义class',
                    type: 'String,Array',
                    optional: '-',
                    default: '-'
                }
            ],
            events: [
                {
                    name: 'onTimeUp',
                    desc: `倒计时结束后触发的方法`,
                    param: `-`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiCountdown,
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {
        constructor(props){
            super(props)
            this.state = {
                endTime:'2020-10-01 20:00:00'
            }
        }

        timeoutDone(){
            console.log('倒计时结束执行的事件')
        }

        render () {
            return (
                <View className='index'>
                    <WiCountdown
                        endTime={this.state.endTime}
                        isShowDay
                        onTimeUp={this.timeoutDone}
                    />
                </View>
            )
        }
}
<\/script>`
        }
    },
}
</script>

